.playerIndex #dplayer .dplayer-controller .dplayer-full-in-icon {
  display: none;
}
.playerIndex #dplayer .dplayer-controller .dplayer-setting {
  display: none;
}
.playerIndex #dplayer .dplayer-controller .dplayer-bar {
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAhcAAAAEAgMAAABAavXHAAAADFBMVEUAAAAAAAAAAAAAAAA16TeWAAAAA3RSTlPmSklSjzZ3AAAAHUlEQVQoz2Po/z8IwAkG+/+DAHwYLM7w/z8IwBEAjY8S1+LXBLcAAAAASUVORK5CYII=);
}
.playerIndex #dplayer .dplayer-controller .dplayer-played {
  background: url(data:image/png;base64,) !important;
}
.playerIndex #dplayer .dplayer-controller .dplayer-full-icon {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 0.35rem 0.35rem;
}
.playerIndex #dplayer .dplayer-controller .dplayer-full-icon svg path {
  opacity: 0;
}
.playerIndex #contentPlayer .video-detail-banner {
  width: 100%;
  height: auto;
  padding-bottom: 0.1rem;
}
.playerIndex #contentPlayer .video-detail-banner img {
  border-radius: 0.1rem;
  width: 100%;
  height: auto;
}
.playerIndex #contentPlayer .title {
  margin-top: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 0.5rem;
  font-size: 0.3rem;
}
.playerIndex #contentPlayer .title .titleText {
  padding: 0 0.2rem;
  width: 4rem;
  flex: 1 1;
}
.playerIndex #contentPlayer .title .lastP {
  white-space: nowrap;
}
.playerIndex #contentPlayer .title .titleLogo img {
  width: 0.5rem;
  height: 100%;
}
.playerIndex .collect {
  margin-top: 0.3rem;
  padding-bottom: 0.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.playerIndex .collect .icon1,
.playerIndex .collect .icon2 {
  width: 0.8rem;
  height: 0.8rem;
  margin-bottom: 0.2rem;
  position: relative;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playerIndex .collect .icon1 span,
.playerIndex .collect .icon2 span {
  position: absolute;
  bottom: -0.3rem;
  left: auto;
}
.playerIndex .collect .icon2 {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 0.4rem 0.4rem;
  margin-left: 0.8rem;
}
.playerIndex .collect .icon1 {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 0.4rem 0.4rem;
}
.playerIndex .collect .icon1Press {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 0.4rem 0.4rem;
  width: 0.8rem;
  height: 0.8rem;
  margin-bottom: 0.2rem;
  position: relative;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playerIndex .collect .icon1Press span {
  position: absolute;
  bottom: -0.3rem;
  left: auto;
}
.playerIndex .collect .icon4,
.playerIndex .collect .icon3 {
  width: 0.9rem;
  height: 0.9rem;
}
.playerIndex .collect .icon3 {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 0.7rem 0.7rem;
}
.playerIndex .collect .icon3Press {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 0.7rem 0.7rem;
  width: 0.9rem;
  height: 0.9rem;
}
.playerIndex .collect .icon4 {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 0.7rem 0.7rem;
  margin-left: 0.3rem;
}
.playerIndex .hotVideo .hotTitle {
  background: url(data:image/png;base64,) no-repeat left center;
  margin: 0.1rem 0;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 0.5rem 0.5rem;
  line-height: 0.5rem;
  height: 0.5rem;
  text-indent: 0.65rem;
  font-size: 0.3rem;
  font-weight: bolder;
}
.playerIndex .hotVideo .collectContent {
  height: auto;
  width: 100%;
  padding-right: 0.2rem;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.playerIndex .hotVideo .collectContent img {
  border-radius: 0.3rem;
}
.playerIndex .hotVideo .collectContent p {
  font-size: 0.28rem;
  margin: 0.05rem 0;
  overflow: hidden;
  /*超出部分隐藏*/
  text-overflow: ellipsis;
  /* 超出部分显示省略号 */
  white-space: nowrap;
  /*规定段落中的文本不进行换行 */
}
.playerIndex .hotVideo .collectContent span {
  color: #bbb5b5;
  font-size: 0.14rem;
}
.playerIndex .hotComment {
  background: url(data:image/png;base64,) no-repeat left center;
  margin: 0.1rem 0;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 0.5rem 0.5rem;
  line-height: 0.5rem;
  height: 0.5rem;
  text-indent: 0.65rem;
  font-size: 0.3rem;
  font-weight: bolder;
}
.playerIndex .InpBottomSay {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.9rem;
  display: flex;
  z-index: 9999;
  background-image: -webkit-gradient(linear, left top, right top, from(#9718ec), to(#68c7e4));
  background-image: linear-gradient(to right, #9718ec, #68c7e4);
  align-items: center;
  padding: 0 0.2rem;
  overflow: hidden;
}
.playerIndex .InpBottomSay textarea {
  background: rgba(255, 255, 255, 0);
  flex: 1 1;
  height: 0.9rem;
  border: 0;
  padding: 0.25rem 0.2rem 0 0.2rem;
  color: #fff;
  font-weight: 900;
  font-size: 0.3rem;
  resize: none;
}
.playerIndex .InpBottomSay textarea::-webkit-input-placeholder {
  color: #dddada;
}
.playerIndex .InpBottomSay textarea::-moz-placeholder {
  color: #dddada;
}
.playerIndex .InpBottomSay textarea:-ms-input-placeholder {
  color: #dddada;
}
.playerIndex .InpBottomSay textarea::-ms-input-placeholder {
  color: #dddada;
}
.playerIndex .InpBottomSay textarea::placeholder {
  color: #dddada;
}
.playerIndex .InpBottomSay .iconPhoto {
  width: 0.4rem;
  height: 0.4rem;
  background: url(data:image/png;base64,);
  background-size: 0.4rem 0.4rem;
}
