.body {
  background-color: #fff;
  height: 100%;
}
.body .feedbackimg {
  margin: 0.1rem 0;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 0.5rem 0.5rem;
  line-height: 0.5rem;
  height: 0.5rem;
  text-indent: 0.65rem;
  font-size: 0.3rem;
  font-weight: bolder;
}
.body .moreFeedBackText {
  float: left;
}
.body .moreFeedBackBtn {
  float: right;
  width: 30%;
}
.body .feedBackStyles {
  display: flex;
  margin-top: 0.3rem;
}
.body .feedBackStyles .feedBackText {
  width: 70%;
  line-height: 0.8rem;
  font-size: 0.3rem;
  font-family: Microsoft YaHei;
}
.body .feedBackStyles .feedBackBtn {
  width: 30%;
  font-size: 0.3rem;
  height: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-width: 1px;
  border-style: solid;
  border-radius: 5rem;
  border-top-color: #38b6e7;
  border-bottom-color: #38b6e7;
  border-left-color: #9718ec;
  border-right-color: #9718ec;
}
