#listTitle {
  padding: 0.2rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#listTitle .titleInfo {
  display: flex;
  align-items: center;
  font-weight: 800;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
#listTitle .titleInfo .icont {
  width: 0.35rem;
  height: 100%;
  margin-right: 0.1rem;
}
#listTitle .more {
  font-size: 0.3rem;
  padding: 0 0.2rem;
  border: 1px solid;
  border-radius: 0.5rem;
  border-top-color: #38b6e7;
  border-bottom-color: #38b6e7;
  border-left-color: #9718ec;
  border-right-color: #9718ec;
}
#listTitle .right {
  font-weight: 800;
  color: #9718ec;
  font-size: 0.3rem;
  display: flex;
  align-items: center;
  line-height: 0.4rem;
}
#listTitle .right .textRight {
  font-weight: 400;
  margin-left: 0.1rem;
  color: #000;
  font-size: 0.24rem;
}
