.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  flex-wrap: wrap;
}
.content .am-list-item {
  padding: 0;
}
.content .am-list-item .am-list-line {
  padding: 0;
}
.content .am-list-item .am-list-line .am-list-content {
  padding-top: 0;
  padding-bottom: 0.01rem;
}
.content .am-list-thumb {
  top: 1.1rem;
  left: 1.55rem;
  z-index: 1;
  position: absolute;
}
.content .am-list-thumb .am-checkbox-inner {
  border-color: #fff;
}
.content .am-list-thumb .am-checkbox-checked .am-checkbox-inner {
  border-color: #7914ee;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 0.4rem 0.4rem;
}
.tabsStyle .list-view-section-body {
  margin-top: 1.5rem;
}
.tabsStyle .am-tabs-tab-bar-wrap {
  width: 100%;
  position: fixed;
  top: 0.8rem;
  left: 0;
  z-index: 2;
}
.tabsStyle .am-tabs-tab-bar-wrap .am-tabs-default-bar-content {
  background-image: -webkit-gradient(linear, left top, right top, from(#9718ec), to(#68c7e4));
  background-image: linear-gradient(to right, #9718ec, #68c7e4);
}
.tabsStyle .am-tabs-tab-bar-wrap .am-tabs-default-bar-content .am-tabs-default-bar-tab-active {
  font-weight: bolder;
}
.tabsStyle .am-tabs-content-wrap {
  margin-top: 0.1rem;
}
