.searchHeader {
  height: 65px;
  background-image: -webkit-gradient(linear, left top, right top, from(#9718ec), to(#68c7e4));
  background-image: linear-gradient(to right, #9718ec, #68c7e4);
  position: relative;
  width: 100%;
}
.searchHeader input {
  border-radius: 0.5rem;
  height: 40px;
  width: 6rem;
  font-size: 0.3rem;
  margin-top: 15px;
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  border: none;
  background-image: -webkit-gradient(linear, left top, right top, from(#7507be), to(#4d9eb9));
  background-image: linear-gradient(to right, #7507be, #4d9eb9);
  padding-left: 0.4rem;
  color: #fff;
}
.searchHeader input::-webkit-input-placeholder {
  color: #ccc;
}
.searchHeader input input::-moz-input-placeholder {
  color: #ccc;
}
.searchHeader input input::-ms-input-placeholder {
  color: #ccc;
}
.searchHeader span {
  color: #fff;
  font-size: 0.3rem;
  font-weight: bolder;
}
.searchHeader .searchIcon {
  width: 30px;
  height: 20px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 20px 20px;
  position: absolute;
  right: 17%;
  top: 25px;
  border-left: 1px solid #fff;
  padding-left: 5px;
}
.am-list-search .am-list-body::before {
  opacity: 0;
}
.am-list-search .am-list-body::after {
  opacity: 0;
}
.am-list-footer div {
  background-color: #fff !important;
}
.searchContentOne {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.2rem;
}
.searchContentOne img {
  margin-right: 0.2rem;
  border-radius: 0.3rem;
}
.searchContentOne .searchText {
  height: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.searchContentOne .searchText h1 {
  font-size: 0.3rem;
  font-weight: normal;
  line-height: 0.35rem;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 3rem;
}
.searchContentOne .searchText .playerNum {
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 0.4rem 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
  text-indent: 0.45rem;
  width: 50%;
}
.searchContentOne .searchText .collectNum {
  margin-left: 0.4rem;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 0.4rem 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
  text-indent: 0.45rem;
}
.searchContentOne .searchText .typeText1 {
  height: 0.5rem;
  width: 1.6rem;
  color: #fff;
  padding: 0 0.2rem;
  line-height: 0.5rem;
  text-align: center;
  background-color: #851be9;
  border-radius: 0.5rem;
  margin-right: 0.4rem;
}
.searchContentOne .searchText .typeText2 {
  height: 0.5rem;
  width: 1.6rem;
  color: #fff;
  padding: 0 0.2rem;
  line-height: 0.5rem;
  text-align: center;
  background-color: #2dc7f7;
  border-radius: 0.5rem;
}
.noSearch {
  height: 2rem;
  font-size: 0.35rem;
  line-height: 2rem;
  text-align: center;
}
