.bookListContentBox {
  width: 33.33333333%;
  padding: 0.05rem;
}
.bookListContentBox .bookListContent {
  width: 100%;
  box-shadow: 0 0 0.1rem #aaa;
  border-radius: 0.2rem;
}
.bookListContentBox .bookListContent .img {
  height: 3rem;
  position: relative;
}
.bookListContentBox .bookListContent .img .bigIMG {
  object-fit: cover;
  border-top-left-radius: 0.2rem;
  border-top-right-radius: 0.2rem;
  width: 100%;
  height: 100%;
  opacity: 1;
  -webkit-transition: 1s;
  transition: 1s;
}
.bookListContentBox .bookListContent .img .bigIMGLoaded {
  opacity: 0;
  -webkit-transition: 1s;
  transition: 1s;
}
.bookListContentBox .bookListContent .img .play {
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  display: flex;
  align-items: center;
  padding: 0.1rem;
  font-size: 0.3rem;
  text-shadow: 0 0 0.1rem #aaa;
}
.bookListContentBox .bookListContent .img .play .icont {
  width: 0.2rem;
  height: 100%;
  margin-right: 0.1rem;
}
.bookListContentBox .bookListContent .img .collected {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  margin: 0.1rem;
}
.bookListContentBox .bookListContent .img .collected img {
  border-radius: 50%;
  width: 0.4rem;
  height: 100%;
  box-shadow: 0 0 0.1rem #aaa;
}
.bookListContentBox .bookListContent .title {
  padding: 0.1rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}
#bookHomeList .am-list-header {
  padding: 0;
}
