.recommendContent {
  flex: 1 1;
  width: 32%;
  position: relative;
  box-shadow: 0 0 0.1rem #aaa;
  border-radius: 0.2rem;
  margin: 0.05rem;
}
.recommendContent .img {
  height: 1.5rem;
}
.recommendContent .img .classTitleImg {
  opacity: 0;
  -webkit-transition: 1s;
  transition: 1s;
}
.recommendContent .img .classTitleImgLoad {
  object-fit: cover;
  border-radius: 0.2rem;
  width: 100%;
  height: 100%;
  opacity: 1 ;
  -webkit-transition: 1s;
  transition: 1s;
}
.recommendContent .title {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0.1rem;
  color: #fff;
  font-weight: 800;
  text-shadow: 0 0 0.1rem #666;
  text-align: center;
}
