#home_book .am-tabs {
  top: 1rem;
}
#home_book .am-search-cancel {
  line-height: 0;
  height: auto;
}
#home_book .am-tabs-top .am-tabs-tab-bar-wrap {
  top: 1rem;
  position: fixed;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0);
  -webkit-transition: 1s;
  transition: 1s;
}
#home_book .am-tabs-default-bar-top .am-tabs-default-bar-nextpage,
#home_book .am-tabs-default-bar-bottom .am-tabs-default-bar-nextpage {
  background: none;
  background-color: rgba(0, 0, 0, 0);
}
#home_book .am-tabs-default-bar-top .am-tabs-default-bar-prevpage,
#home_book .am-tabs-default-bar-bottom .am-tabs-default-bar-prevpage {
  background: none;
  background-color: rgba(0, 0, 0, 0);
}
#home_book .am-tabs-default-bar-top {
  background-color: rgba(0, 0, 0, 0) !important;
}
#home_book .am-tabs-default-bar-tab {
  color: #fff;
  height: 0.5rem;
}
#home_book .am-tabs-default-bar-tab-active {
  color: #fff600;
  font-weight: 800;
}
#home_book .am-tabs-default-bar-underline {
  border: 1px #fff600 solid;
}
#home_book .am-list-body {
  padding: 0 0.2rem;
}
#home_book .player {
  position: fixed;
  bottom: 1.2rem;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1000;
}
#home_book .player .changeWindow {
  position: absolute;
  top: 0.2rem;
  width: 4rem;
  margin-left: 1.3rem;
  height: 0.5rem;
  background-color: rgba(255, 255, 255, 0);
  z-index: 1000;
}
#home_book .player .offPlaye {
  position: absolute;
  right: 0.17rem;
  top: 0.17rem;
  z-index: 1000;
  box-shadow: 0 0 0.2rem #aaa;
  border-radius: 1rem;
  width: 0.4rem;
  height: 0.4rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
#home_book .player .offPlaye img {
  width: 100%;
  height: 100%;
}
#home_book .player .offPlaye:active {
  box-shadow: none;
}
#home_book .player .playerSerial {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  right: 0.5rem;
  bottom: 2.5rem;
  z-index: 1000;
  padding: 0.1rem;
  max-height: 5rem;
  overflow-y: auto;
  border-radius: 0.1rem;
  box-shadow: 0 0 0.2rem #000;
}
#home_book .player .playerSerial p {
  line-height: 0.4rem;
  border: 1px solid #aaa;
  margin-bottom: 0.03rem;
  text-align: center;
  border-radius: 0.1rem;
  font-size: 0.24rem;
  color: #eee;
  padding: 0 0.1rem;
  box-shadow: 0 0 0.1rem #aaa;
}
#home_book .player .playerSerial p:active {
  box-shadow: none;
}
#home_book .player .playerSerial .activeSerial {
  color: #9718ec;
}
#home_book .player .aplayer {
  box-shadow: 0 0 0.1rem #111;
  padding: 0.1rem;
  background-color: rgba(0, 0, 0, 0.8);
}
#home_book .player .select {
  position: absolute;
  right: 0.67rem;
  top: 0.2rem;
  z-index: 1001;
  color: #aaa;
  border: 1px solid #aaa;
  padding: 0 0.1rem;
  border-radius: 0.1rem;
  box-shadow: 0 0 0.1rem #aaa;
}
#home_book .player .select:active {
  box-shadow: none;
}
#home_book .player .aplayer-pic {
  height: 1rem;
  width: 1rem;
}
#home_book .player .aplayer-lrc {
  display: none;
}
#home_book .player .aplayer-info {
  height: auto;
  margin: 0;
  padding: 0;
}
#home_book .player .aplayer-info .aplayer-music {
  margin-left: 1.2rem;
}
#home_book .player .aplayer-info .aplayer-music .aplayer-title {
  color: #eee;
}
#home_book .player .aplayer-info .aplayer-music .aplayer-author {
  color: #aaa;
}
#home_book .player .aplayer-info .aplayer-controller {
  margin-top: 0.23rem;
}
#home_book .player .aplayer-info .aplayer-controller .aplayer-bar .aplayer-played {
  background: url(data:image/png;base64,) no-repeat !important;
}
#home_book .player .aplayer-info .aplayer-controller .aplayer-bar .aplayer-thumb {
  background: url(data:image/png;base64,) no-repeat !important;
  background-size: cover !important;
  width: 20px !important;
  height: 20px !important;
  margin-top: -10px !important;
  margin-right: -10px !important;
  top: 0 !important;
  right: 0 !important;
}
#home_book .player .aplayer-info .aplayer-controller .aplayer-time .aplayer-volume-wrap .aplayer-icon-volume-down {
  display: none !important;
}
#home_book .player .aplayer-music {
  margin: 0;
}
#home_book .list-view-section-body {
  justify-content: flex-start;
}
