.imgpicker .am-image-picker-list {
  overflow: hidden;
}
.imgpicker .am-image-picker-list .am-flexbox.am-flexbox-align-center {
  overflow: inherit;
  height: 1.8rem;
}
.imgpicker .am-image-picker-list .am-flexbox.am-flexbox-align-center .am-flexbox-item {
  margin-right: 0.09rem;
  margin-left: 0.09rem;
}
.imgpicker .am-image-picker-list .am-flexbox.am-flexbox-align-center .am-flexbox-item .am-image-picker-item .am-image-picker-item-remove {
  background-color: #f700ff;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  position: absolute;
  right: -0.13rem;
  top: -0.13rem;
  background-image: url(data:image/png;base64,) center center;
  background-size: 0.4rem 0.4rem;
  z-index: 999;
}
.subMit {
  position: fixed !important;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.9rem;
  background-image: -webkit-gradient(linear, left top, right top, from(#9718ec), to(#68c7e4));
  background-image: linear-gradient(to right, #9718ec, #68c7e4);
  border-radius: 0;
  z-index: 1;
}
