#headerHome {
  -webkit-transition: 1s;
  transition: 1s;
}
#headerHome .header-search {
  display: flex;
  width: 100vw;
  height: 1rem;
  background-color: #fff;
  padding: 0.2rem;
  align-items: center;
  justify-content: space-around;
  -webkit-transition: 1s;
  transition: 1s;
}
#headerHome .header-search .envelope {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 0.1rem;
}
#headerHome .header-search .envelope:active {
  background-color: rgba(0, 0, 0, 0.4);
}
#headerHome .header-search .my {
  width: 0.5rem;
  height: 0.5rem;
  margin-left: 0.1rem;
  border-radius: 0.1rem;
  position: relative;
}
#headerHome .header-search .my:active {
  background-color: rgba(0, 0, 0, 0.4);
}
#headerHome .header-search .search {
  flex: 1 1;
  padding: 0 0.2rem;
  display: flex;
  padding: 0.2rem;
  border-radius: 1rem;
  background-color: rgba(40, 40, 40, 0.3);
  align-items: center;
  height: 0.6rem;
  margin-left: 0.2rem;
}
#headerHome .header-search .search span {
  flex: 1 1;
  text-align: left;
  color: #fff;
  margin-right: 0.13rem;
  font-weight: 100;
  border-right: 1px #fff solid;
}
#headerHome .header-search .search img {
  width: 0.45rem;
  vertical-align: middle;
}
#headerHome .background {
  -webkit-transition: 1s;
  transition: 1s;
}
.am-tab-bar-bar {
  -webkit-transition: 1s;
  transition: 1s;
}
