.am-list-video .am-list-body::before {
  opacity: 0;
}
.am-list-video .am-list-body::after {
  opacity: 0;
}
.am-list-video .am-list-body .list-view-section-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.am-list-video .am-list-footer {
  opacity: 1;
}
.am-list-video .am-list-footer div {
  background-color: #fff !important;
}
.collectContent {
  width: 3.5rem;
  height: 3rem;
  margin: 0.1rem;
  box-sizing: border-box;
  text-align: center;
}
.collectContent img {
  border-radius: 0.3rem;
}
.collectContent p {
  font-size: 0.28rem;
  margin: 0.05rem 0;
  overflow: hidden;
  /*超出部分隐藏*/
  text-overflow: ellipsis;
  /* 超出部分显示省略号 */
  white-space: nowrap;
  /*规定段落中的文本不进行换行 */
}
.collectContent span {
  color: #bbb5b5;
  font-size: 0.14rem;
}
.collectContentBook {
  width: 3.5rem;
  height: 6rem;
  margin: 0.1rem;
  box-sizing: border-box;
  text-align: center;
}
.collectContentBook img {
  border-radius: 0.3rem;
}
.collectContentBook p {
  font-size: 0.28rem;
  margin: 0.05rem 0;
  overflow: hidden;
  /*超出部分隐藏*/
  text-overflow: ellipsis;
  /* 超出部分显示省略号 */
  white-space: nowrap;
  /*规定段落中的文本不进行换行 */
}
.collectContentBook span {
  color: #bbb5b5;
  font-size: 0.14rem;
}
